.menu-bar {
   width: 100%;
   background-color: var(--second-color);
   color: var(--main-color);
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 1rem 5rem;
   position: fixed;
   z-index: 199999999999999;
}
.logo-circle {
   width: 50px;
   height: 50px;
   border-radius: 50%;
   background-color: var(--main-color);
   cursor: pointer;
}
.menu-bar ul {
   list-style: none;
   margin: 0;
   padding: 0;
   display: flex;
}
.menu-bar li {
   margin-left: 2rem;
   font-family: "Poppins", sans-serif;
   cursor: pointer;
}
.menu-bar a {
   text-decoration: none;
   color: var(--main-color);
}

.logo-circle {
   width: 50px;
   height: 50px;
   border-radius: 50%;
   background-color: var(--main-color);
   cursor: pointer;
}
